@import url(https://fonts.googleapis.com/css2?family=Fugaz+One&display=swap);
header {
  min-height: 70px;
  width: 100%;
  overflow: hidden;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  border-bottom: 1px solid #ddd;
  background: white;
}
.logo h1 a {
  font-family: 'Fugaz One', cursive;
  text-transform: capitalize;
  margin-left: 10px;
}
header .logo {
  flex: 1 1;
}

header a {
  text-transform: uppercase;
  color: #325894;
}

header ul li {
  display: inline-block;
  padding: 0 20px;
  color: #5a9d91;
}
header ul li:hover {
  color: #5a9d91;
}
.menu {
  display: none;
}
.cart-icon {
  position: relative;
  margin-right: 20px;
}
.cart-icon span {
  background: #e25578;
  border-radius: 20px;
  color: white;
  position: absolute;
  top: -10px;
  right: -10px;
  padding: 5px 7px;
  font-size: 10px;
}

@media only screen and (max-width: 500px) {
  h1 {
    font-size: large;
  }
}

.product_card {
  max-width: 300px;
  overflow: hidden;
  height: 500px;
  padding: 15px;
  box-shadow: 0 0 15px #ddd;
  margin: 10px 0;
  position: relative;
  background: white;
}
.product_card img {
  width: 100%;
  height: 300px;
  display: block;
  object-fit: cover;
}
.product_box h2 {
  width: 100%;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  text-transform: capitalize;
  cursor: pointer;
  color: #323232;
}
.product_card span {
  color: #e25578;
  font-weight: 600;
}
.product_box p {
  width: 100%;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  height: 70px;
  overflow: hidden;
  color: #323232;
}
.row_btn {
  width: 100%;
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
}
.row_btn a {
  width: 50%;
  text-align: center;
  text-transform: uppercase;
  color: white;
  font-weight: 600;
  letter-spacing: 2px;
  padding: 6px;
}
#btn_buy {
  background: #e76443;
  margin-right: 5px;
}
#btn_view {
  background: #325894;
  margin-left: 5px;
}
.product_box {
}
.product_card input {
  position: absolute;
  width: 25px;
  height: 25px;
}

.products {
  width: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  justify-items: center;
  margin: 20px 0;
  padding: 30px 0;
  border-radius: 5px;
}

/* ----------- Product Item ----------------- */

.delete-all {
  text-align: right;
  margin: 20px;
}
.delete-all input {
  height: 25px;
  width: 25px;
  transform: translateY(5px);
  margin: 0 15px;
}
.delete-all span {
  text-transform: uppercase;
  color: #5a9d91;
  letter-spacing: 1.3px;
}
.delete-all button {
  border: 1px solid #e25578;
  padding: 10px 25px;
  color: #e25578;
  text-transform: uppercase;
}

/* ------------ Filters Menu ----------------- */
.filter_menu {
  width: 100%;
  min-height: 40px;
  overflow: hidden;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  margin: 15px 0;
}
.filter_menu select,
input {
  border: 1px solid #c2c7d2;
  outline: none;
  height: 40px;
  padding: 0 5px;
}
.filter_menu input {
  flex: 1 1;
  margin: 0 10px;
}

/* -------------------- Load More --------------- */
.load_more {
  text-align: center;
}
.load_more button {
  padding: 10px 25px;
  margin-bottom: 20px;
  border: 1px solid #555;
  text-transform: capitalize;
}

.detail {
  width: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  padding: 50px;
  font-size: 150%;
  background: #c2c7d225;
  border-radius: 5px;
  margin: 20px 0;
}
.detail img {
  max-width: 400px;
  width: 100%;
  margin: 20px;
  height: 450px;
  object-fit: cover;
  display: block;
}
.box-detail {
  max-width: 500px;
  width: 100%;
  margin: 5px 20px;
}
.box-detail .row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.box-detail h2 {
  text-transform: uppercase;
  color: #325894;
  letter-spacing: 2px;
  font-weight: 2rem;
}
.box-detail p {
  line-height: 1.5;
  margin: 10px 0;
  opacity: 0.8;
}
.box-detail span {
  color: #e25578;
  font-weight: 600;
  font-size: xx-large;
}
.box-detail .cart {
  background: #e76443;
  color: white;
  margin-top: 10px;
  padding: 10px 25px;
  display: inline-block;
  text-transform: uppercase;
  letter-spacing: 1px;
  border: solid 1px rgba(0, 0, 0, 0.199);
  font-weight: 500;
}
.contact {
  margin-top: 20px;
  background: #ffffff;
  padding: 20px;
  border-radius: 10px;
  text-align: center;
}
.contact a {
  color: #325894;
  font-weight: 600;
}

.login-page {
  max-width: 500px;
  border: 2px solid #5a9d91;
  border-radius: 5px;
  padding: 30px;
  margin: 50px auto;
}
.login-page h2 {
  text-transform: uppercase;
  letter-spacing: 2px;
  color: #555;
}
.login-page form input,
.login-page form button {
  width: 100%;
  height: 40px;
  margin: 10px 0;
  padding: 0 5px;
  outline: #5a9d91;
  border: 1px solid #5a9d91;
}
.login-page form .row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.login-page form button {
  width: 150px;
  background: #325894;
  color: white;
  text-transform: uppercase;
  letter-spacing: 2px;
}
.login-page form a {
  color: #eca346;
  letter-spacing: 1.3px;
  text-transform: uppercase;
}

.cart {
  position: relative;
  border: 1px solid #ccc;
  transform: scaleY(0.98);
  overflow: hidden;
}
.amount span {
  color: #e25578;
  padding: 0 20px;
}
.amount button {
  width: 40px;
  height: 40px;
  border: 1px solid #777;
}
.delete {
  position: absolute;
  top: 0;
  right: 5px;
  color: #e25578;
  font-weight: 900;
  cursor: pointer;
}
.total {
  width: 100%;
  /* height: 50px; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 25px;
  overflow: hidden;
}
.total h3 {
  color: #325894;
  font-size: xx-large;
  /* margin-bottom: 50px; */
}

.history-page {
  overflow-x: auto;
}
.history-page h2,
h4 {
  text-align: center;
  margin: 20px;
  text-transform: uppercase;
  letter-spacing: 1.2px;
}
.history-page table {
  margin: auto;
  width: 100%;
}
.history-page table,
th,
tr,
td {
  border: 1px solid #ddd;
  border-collapse: collapse;
}
th,
td {
  text-align: center;
  padding: 10px;
  text-transform: capitalize;
}
table a {
  color: #325894;
}

.history-page img {
  width: 70px;
  height: 100px;
  object-fit: cover;
}

.categories{
    max-width: 700px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    margin: 30px auto;
}
.categories form{
    width: 290px;
    margin-bottom: 20px;
}
.categories label{
    display: block;
    font-weight: 700;
    letter-spacing: 2px;
    text-transform: uppercase;
    margin-bottom: 10px;
}
.categories input, button{
    height: 35px;
    border: none;
    outline: none;
    border-bottom: 1px solid #555;
}
.categories input{
    width: 210px;
}
.categories button{
    width: 70px;
    background: #555;
    color: white;
    margin-left: 10px;
}
.categories .row{
    min-width: 290px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    margin-bottom: 10px;
    border: 1px solid #ccc;
}
.create_product {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
}
.upload {
  max-width: 450px;
  height: 500px;
  width: 100%;
  border: 1px solid #ddd;
  padding: 15px;
  margin: 20px;
  position: relative;
}
#file_up {
  position: relative;
  width: 100%;
  height: 100%;
  outline: none;
}
#file_up::before {
  content: '+';
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: white;
  color: #eca346;
  font-size: 17rem;
  text-align: center;
  cursor: pointer;
  margin: auto;
}
#file_img {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: white;
}
#file_img img {
  width: 100%;
  height: 100%;
  display: block;
  object-fit: cover;
}
#file_img span {
  position: absolute;
  top: -13px;
  right: -13px;
  background: white;
  border: 1px solid #c2c7d2;
  border-radius: 50%;
  padding: 6px 10px;
  cursor: pointer;
  font-weight: 900;
  color: #e25578;
}

.create_product form {
  max-width: 500px;
  min-width: 290px;
  width: 100%;
  margin: 15px 30px;
}
.create_product form .row {
  width: 100%;
  margin: 15px 0;
}
.create_product form input,
textarea {
  width: 100%;
  min-height: 40px;
  padding: 0 5px;
}
.create_product form button {
  width: 200px;
  height: 40px;
  background: #325894;
  color: white;
  text-transform: uppercase;
  letter-spacing: 2px;
  font-weight: 700;
}

* {
  padding: 0;
  box-sizing: border-box;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.App {
  max-width: 1400px;
  min-height: 100vh;
  margin: 0 auto;
  padding: 0 20px;
  box-shadow: 0 0 35px #eee;
}
a {
  text-decoration: none;
}
ul li {
  list-style: none;
}
.products {
  background-image: url(/static/media/the-latin-everything-background.c89ef9d8.PNG);
}
button {
  border: none;
  outline: none;
  background: transparent;
  cursor: pointer;
}

