/* ----------------- Responsive ------------------ */
@media (max-width: 820px) {
  header .logo {
    min-width: 115px;
    flex: none;
  }
  header ul {
    padding: 10px 0;
  }
}
@media (max-width: 720px) {
  header ul {
    position: fixed;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100vh;
    background: white;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    opacity: 0.98;
    z-index: 99;
    transition: 0.5s ease-in;
  }
  header .menu {
    display: block;
    cursor: pointer;
  }
  header ul li .menu {
    position: absolute;
    top: 20px;
    right: 20px;
  }
  header {
    justify-content: space-between;
  }
}

@media (max-width: 570px) {
  .filter_menu .row span {
    display: none;
  }
}

@media (max-width: 500px) {
  .App {
    padding: 0 10px;
  }
  .detail {
    font-size: 100%;
    padding: 0;
  }
  .filter_menu input {
    margin: 0;
  }
  .filter_menu .sort {
    flex: 1 1;
  }
  .filter_menu .sort select {
    width: 100%;
    min-width: 290px;
    margin: 5px 0;
  }
  .login-page {
    padding: 30px 15px;
  }
  .delete-all {
    margin: 20px 0;
  }
  .create_product .upload {
    margin: 20px 0;
  }
}

.load-page {
  background: #fff;
  overflow: hidden;
}
.loader {
  width: 300px;
  height: 300px;
  margin: auto;
}
.loader div {
  width: calc(100% - 15px);
  height: calc(100% - 15px);
  border: 2px solid #fff;
  border-top: 2px solid #e25578;
  border-radius: 50%;
  animation: rotate 10s linear infinite alternate-reverse;
}
@-webkit-keyframes rotate {
  50% {
    transform: rotate(80deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
@keyframes rotate {
  50% {
    transform: rotate(80deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

